
.useful-information {
	margin-top: 146px;
}

.useful-information__top {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	margin-bottom: 51px;
}

.useful-information__title {
	margin-top: 0;
}

.useful-information__link-top {
	margin-bottom: 7px;
	& > span {
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 28px;
		color: $color-main;
		margin-right: 56px;
	}
	& > svg {
		fill: $color-main;
		width: 20px;
		height: 15px;
	}
}

.useful-information__slide {
	outline: none;
	height: 327px;
	width: 363px;
}

.useful-information__slide-wrap {
	display: flex;
	flex-direction: column;
	width: 311px;
}

.useful-information__slide-img {
	width: 100%;
	height: 195px;
}

.useful-information__slide-title {
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 28px;
	text-transform: uppercase;
	color: #808080;
	margin: 5px 0 0;
}

.useful-information__slide-desc {
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 26px;
	margin: 0 0 20px;
	height: 52px;
	width: 90%;
}

.useful-information__slide-link-wrap {
	width: 100%;
	display: flex;
	justify-content: flex-end;
}

.useful-information__slide-link {
	& > span {
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 28px;
		color: $color-main;
		margin-right: 23px;
	}
	& > svg {
		fill: $color-main;
		width: 11px;
		height: 7px;
	}
}

.useful-information__arrow  {
	bottom: -101px;
}
