.questions {
	position: relative;
	overflow: hidden;
	padding-bottom: 180px;
}

.questions__sguares {
	position: absolute;
	left: 61%;
	top: 244px;
	z-index: -1;
	width: max-content;
	.square {
		background: $color-main;
	}
}
.questions__title {
	text-align: center;
	margin-top: 195px;
	margin-bottom: 51px;
}

.questions__accordion {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.questions__accordion-item {
	width: 970px;
	background: #FFFFFF;
	border: 1px solid #F57E23;
	box-sizing: border-box;
	border-radius: 3px;
	margin-bottom: 20px;
	padding: 18px 10px 9px 30px;
}

.questions__accordion-top {
	display: flex;
	justify-content: space-between;
	cursor: pointer;
}

.questions__accordion-title {
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 28px;
	padding-bottom: 7px;
	user-select: none;
	max-width: 90%;
}

.questions__accordion-arrow {
	width: 20px;
	height: 16px;
	background: transparent;
	transform: translateY(10px) rotate(90deg);
	border: none;
	margin: 0;
	padding: 0;
	cursor: pointer;
	outline: none;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: all .3s;
	& > svg {
		width: 15px;
		height: 15px;
	}
}

.questions__accordion-desc-wrap {
	overflow: hidden;
	max-height: 0;
	transition: all .3s;
}

.questions__accordion-desc {
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 24px;
	padding-top: 28px;
	max-width: 90%;
	& > a {
		color: $color-black;
	}
}

.questions__accordion-item--active {
	.questions__accordion-desc-wrap {
		max-height: 500px;
	}
	.questions__accordion-arrow {
		transform:  translateY(10px) rotate(-90deg);
	}
}
.questions__form-title {
	font-style: normal;
	font-weight: 500;
	font-size: 48px;
	line-height: 120%;
	text-align: center;
	margin: 78px 0 15px;
}

.questions__form-par {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
	margin: 0 0 45px;
	text-align: center;
}

.questions__wrap-inputs {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.questions__top-inputs {
	width: 682px;
	display: flex;
	justify-content: space-between;
	margin-bottom: 30px;
}

.questions__input-name {
	width: 313px;
}
.questions__input-mail {
	width: 338px;
}

.questions__bottom-inputs .questions__textarea {
	width: 682px;
}

.questions__person {
	width: 682px;
	margin-top: 10px;
	& > span {
		display: inline-block;
		font-style: normal;
		font-weight: normal;
		font-size: 12px;
		line-height: 150%;
		& > a {
			color: $color-black;
		}
	}
}

.questions__btn-wrap {
	width: 100%;
	display: flex;
	justify-content: center;
	margin-top: 27px;
}
