.header {
	position: sticky;
	left: 0;
	top: 0;
	z-index: 10;
	margin: 0;
	padding: 0;
	width: 100%;
	max-height: 96px;
	background-color: $color-white;
}

.header__wrap {
	display: flex;
	justify-content: space-between;
}

.header__logo {
	display: block;
	padding: 35px 0;
	width: 150px;
	height: 26px;
	text-decoration: none;
}

.nav {
	margin: 0;
	padding: 0;
}

.nav__wrap {
	display: flex;
}

.nav__list {
	display: flex;
	justify-content: space-between;
	list-style: none;
	margin: 0;
	padding: 0;
}

.nav__item {
	display: block;
	padding: 42px 20px 35px;
	font-style: normal;
	font-size: 1rem;
	line-height: 19px;
	white-space: nowrap;
	text-decoration: none;
	text-transform: uppercase;
	color: $color-black;
	&:hover {
		color: $color-main;
	}
}

.nav__lang {
	display: block;
	padding: 44px 2px 36px 72px;
	font-style: normal;
	font-size: 0.875rem;
	line-height: 16px;
	white-space: nowrap;
	text-decoration: none;
	text-transform: uppercase;
	color: $color-black;
	&:hover {
		color: $color-main;
	}
}
