.useful-instruction {

}

.useful-instruction__title {
	margin-bottom: 50px;
}

.useful-instruction__wrap {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.useful-instruction__item {
	margin-bottom: 20px;
	max-width: 49.5%;
}

.useful-instruction__item-title {
	font-style: normal;
	font-weight: 500;
	font-size: 36px;
	line-height: 42px;
	margin: 0 0 30px;
	color: $color-main;
	max-width: 590px;
}

.useful-instruction__item-img {
	width: 100%;
	height: 400px;
	margin-bottom: 30px;
	& > img {
		max-width: 100%;
		max-height: 100%;
	}
}

.useful-instruction__item-document {
	.documents__document {
		width: 100%;
	}
}
