.breadcrumbs {
	margin-top: 50px;
	margin-bottom: 52px;
}

.breadcrumbs__wrap {
	display: flex;
}

.breadcrumb {
	text-decoration: none;
	font-style: normal;
	font-weight: normal;
	font-size: 14px;
	line-height: 16px;
	text-transform: uppercase;
	color: #6D6D6D;
}

a.breadcrumb {
	&::after {
		content: '/';
		font-style: normal;
		font-weight: normal;
		font-size: 14px;
		line-height: 16px;
		margin: 0 5px;
		text-transform: uppercase;
		color: #6D6D6D;
	}
	&:hover {
		color: #4D4D4D;
	}
}
