.useful-documents {
	padding-bottom: 29px;
}

.useful-documents__title {
	margin-bottom: 50px;
	margin-top: 68px;
}

.useful-documents__wrap {
	display: flex;
}
.useful-documents__item {
	width: 33%;
}

.useful-documents__item-title {
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 28px;
	text-transform: uppercase;
	min-height: 56px;
	margin-bottom: 45px;
	max-width: 350px;
}

.useful-documents__item-list {
	margin: 0;
	padding: 0;
	list-style: none;
	li {
		margin-bottom: 20px;
		.documents__document {
			width: 100%;
			margin-bottom: 33px;
		}
		.documents__document-title {
			max-width: 275px;
		}
	}
}

