.questions-content {
	position: relative;
	z-index: 5;
	padding-bottom: 60px;
	.questions__accordion-item--active {
		background: $color-white-active;
	}
	.questions__accordion-item {
		padding: 51px 40px 38px 40px;
		margin-bottom: -1px;
		width: 974px;
	}
	.questions__accordion-desc {
		font-size: 18px;
		line-height: 25px;
		padding-top: 24px;
	}
}

.questions-content__wrap {
	margin-top: -425px;
}

.pagination {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-top: 60px;
	margin-bottom: 40px;
}

.pagination-arrow {
	width: 15px;
	height: 15px;
	cursor: pointer;
	&--left {
		margin-right: 35px;
		transform: rotate(180deg);
	}
	&--right {
		margin-left: 35px;
	}
}

.pagination-item {
	width: 38px;
	height: 38px;
	display: flex;
	border-radius: 50%;
	justify-content: center;
	align-items: center;
	margin: 0 5px;
	cursor: pointer;
	text-decoration: none;
	&--active {
		background: #F57E23;
		box-shadow: 0px 0px 10px rgba(235, 94, 54, 0.6);
		color: $color-white;
	}
}
