.footer {
	background: $color-black-bg;
	padding-top: 113px;
	padding-bottom: 20px;
}

.footer__wrap {
	display: flex;
}

.footer__logo {
	width: 30%;
	flex-shrink: 0;
}

.footer__form {
	width: 39%;
	flex-shrink: 0;
}

.footer__form-title {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 21px;
	color: $color-white;
	max-width: 240px;
	margin-bottom: 38px;
}

.footer__form-input {
	border: none;
	border-bottom: 1px solid $color-white;
	color: $color-white;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 19px;
	padding: 10px 10px 11px 0;
	margin-bottom: 13px;
	background: transparent;
	width: 330px;
	outline: none;
	&::placeholder {
		color: $color-white;
	}
}

.footer__checkbox {
	display: block;
	margin-bottom: 40px;
	.checkbox__text {
		color: $color-white;
		font-style: normal;
		font-weight: normal;
		font-size: 12px;
		line-height: 150%;
		& > a {
			color: $color-white;
		}
	}
	.checkbox__text::before {
		border-color: $color-white;
	}
	.checkbox__text::after {
		content: url("/images/sprites/svg/check-white.svg");
	}
}

.footer__nav {
	width: 25%;
	flex-shrink: 0;
}

.footer__nav-list {
	list-style: none;
	margin: 0;
	padding: 0;
}

.footer__nav-item {
	margin-bottom: 18px;
	& > a {
		font-style: normal;
		font-weight: normal;
		font-size: 16px;
		line-height: 19px;
		text-decoration: none;
		color: $color-white;
	}
}

.footer__nav-lang {
	& > a {
		font-style: normal;
		font-weight: normal;
		font-size: 14px;
		line-height: 16px;
		color: $color-white;
		text-decoration: none;
	}
}

.footer__social {
	width: 6%;
	display: flex;
	height: max-content;
	list-style: none;
	margin: 0;
	padding: 0;
	align-items: center;
	justify-content: flex-start;
}

.footer__social-item {
	margin-right: 30px;
}

.footer__social-svg {
	max-width: 29px;
	max-height: 29px;
	fill: $color-white;
	&--instagram {
		max-width: 19px;
		max-height: 19px;
	}
}

.footer__bottom {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	margin-top: 40px;
}

.footer__dev {
	font-style: normal;
	font-weight: normal;
	font-size: 14px;
	line-height: 16px;
	color: $color-white;
	a {
		color: $color-white;
		text-decoration: none;
		text-transform: uppercase;
	}
}

.footer__info {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 21px;
	max-width: 420px;
	text-transform: uppercase;
	color: $color-white;
	margin-right: 7px;
}
