
.monitoring {
	padding-top: 38px;
}
.monitoring__info {
	width: 770px;
}

.monitoring__title {
	max-width: 1120px;
	padding-bottom: 14px;
}

.monitoring__wrap {
	display: flex;
}

.monitoring__par {
	margin-top: 27px;
	max-width: 620px;
}


.monitoring__subtitle {
	margin-top: 42px;
	margin-bottom: 58px;
}

.monitoring__list {
	margin: 0;
	padding: 0;
	list-style: none;
	width: 660px;
	height: 500px;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	max-height: 212px;
	min-width: 769px;
}

.monitoring__item {
	margin-bottom: 41px;
	position: relative;
}

.monitoring__item-number {
	position: absolute;
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 24px;
	bottom: 70%;
}

.monitoring__item-text {
	display: inline-block;
	margin-left: 35px;
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
}

.monitoring__images {
	position: relative;
}

.monitoring__img {
	position: absolute;
	&::after {
		position: absolute;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		background-color: $color-main;
		color: $color-white;
		font-style: normal;
		font-weight: 500;
		font-size: 24px;
		line-height: 26px;
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 3;
	}
	&--1 {
		top: 83px;
		left: 129px;
		z-index: 2;
		&::after {
			content: '01';
			bottom: 20px;
			left: 5px;
		}
	}
	&--2 {
		top: 1px;
		left: -48px;
		z-index: 1;
		&::after {
			content: '02';
			top: 123px;
			left: 0;
		}
	}
	&--3 {
		top: 360px;
		left: 350px;
		z-index: 2;
		&::after {
			content: '03';
			bottom: 24px;
			right: 33px;
		}
	}
	&--4 {
		top: 274px;
		left: 17px;
		z-index: 2;
		&::after {
			content: '04';
			top: -43px;
			left: 43px;
		}
	}
	&--5 {
		left: 360px;
		top: 210px;
		z-index: 2;
		&::after {
			content: '05';
			top: -48px;
			right: -6px;
		}
	}
	&--6 {
		left: 60px;
		top: 407px;
		&::after {
			content: '06';
			top: 16px;
			left: 57px;
		}
	}
}
