.squares {
	display: flex;
	flex-wrap: nowrap;
}

.square {
	flex-shrink: 0;
	background: #FFFFFF;
	opacity: 0;
	&--1 {
		opacity: 0.05;
	}
	&--2 {
		opacity: 0.1;
	}
	&--3 {
		opacity: 0.15;
	}
	&--4 {
		opacity: 0.2;
	}
	&--5 {
		opacity: 0.25;
	}
	&--6 {
		opacity: 0.3;
	}
	&--7 {
		opacity: 0.35;
	}
	&--8 {
		opacity: 0.4;
	}
	&--9 {
		opacity: 0.45;
	}
	&--10 {
		opacity: 0.5;
	}
	&--11 {
		opacity: 0.55;
	}
	&--12 {
		opacity: 0.6;
	}
	&--13 {
		opacity: 0.65;
	}
	&--14 {
		opacity: 0.7;
	}
	&--15 {
		opacity: 0.75;
	}
	&--16 {
		opacity: 0.8;
	}
	&--17 {
		opacity: 0.85;
	}
	&--18 {
		opacity: 0.9;
	}
	&--19 {
		opacity: 0.95;
	}
	&--20 {
		opacity: 1;
	}
}
