
.btn {
	display: flex;
	align-items: center;
	justify-content: space-between;
	border: 1px solid $color-main;
	border-radius: 50px;
	padding: 9px;
	width: 262px;
	height: max-content;
	box-sizing: border-box;
	text-decoration: none;
	color: $color-white;
	background: $color-main;
	transition: all .3s;
	cursor: pointer;
	outline: none;
	&__text {
		display: block;
		padding-left: 20px;
		height: 100%;
		font-weight: 500;
		font-style: normal;
		font-size: 18px;
		line-height: 21px;
	}
	&__svg {
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		width: 50px;
		height: 50px;
		box-shadow: 0 0 10px rgba(235, 94, 54, 0.6);
		background: $color-white;
		transition: background-color .3s;
		& > svg {
			width: 20px;
			fill: $color-main;
			transition: fill .3s;
		}
	}

	&:hover {
		background: $color-main-active;
		.btn__svg svg {
			fill: $color-main-active;
		}
	}
}

.btn--min {
	width: 200px;
	padding: 7px 7px 7px 9px;
	.btn__svg {
		width: 38px;
		height: 38px;
		& > svg {
			width: 15px;
		}
	}
}

.btn--white {
	border-color: $color-white;
	color: $color-main;
	background: $color-white;
	.btn__svg {
		background: $color-main;
		& > svg {
			fill: $color-white;
		}
	}
	&:hover {
		background: $color-white-active;
		.btn__svg {
			background: $color-main-active;
			svg {
				fill: $color-white-active;
			}
		}
	}
}

.btn--outline {
	border-color: $color-main;
	color: $color-main;
	background: $color-white;
	.btn__svg {
		background: $color-main;
		& > svg {
			fill: $color-white;
		}
	}
	&:hover {
		background: $color-main-active;
		color: $color-white;
		.btn__svg {
			background: $color-white;
			svg {
				fill: $color-main-active;
			}
		}
	}
}

.btn--white.btn--outline {
	border-color: $color-white;
	color: $color-white;
	background: transparent;

	&:hover {
		background: $color-white;
		color: $color-main-active;
		.btn__svg {
			background: $color-main-active;
			svg {
				fill: $color-white;
			}
		}
	}
}

.btn-slide {
	position: absolute;
	bottom: -101px;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 38px;
	height: 38px;
	background: $color-main;
	box-shadow: 0px 0px 10px rgba(235, 94, 54, 0.6);
	border-radius: 50%;
	cursor: pointer;
	transition: all .3s;
	& > svg {
		fill: $color-white;
		width: 17px;
		height: 17px;
	}
	&:hover {
		background: $color-main-active;
	}
}

.btn-slide--left {
	right: 68px;
	transform: rotate(180deg);
}

.btn-slide--right {
	right: 0;
}
