.useful-banner {
	background: $color-main;
	padding-top: 214px;
	height: 695px;
	position: relative;
	overflow: hidden;
}

.useful-banner__wrap {
	position: relative;
	z-index: 3;
}

.useful-banner__title {
	font-style: normal;
	font-weight: 500;
	font-size: 72px;
	line-height: 84px;
	margin: 0 0 30px;
	color: $color-white;
	max-width: 665px;
}

.useful-banner__par {
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 34px;
	margin: 0;
	color: $color-white;
	max-width: 610px;
}

.useful-banner__sguares {
	position: absolute;
	right: -56%;
	top: 53px;
}

.useful-banner__ellipse {
	position: absolute;
	z-index: 0;
	width: 975px;
	height: 975px;
	left: 49px;
	top: -167px;
	background: linear-gradient(147.31deg, #F57E23 26.7%, #FDC489 119.84%);
	border-radius: 50%;
}

.useful-banner__pluses {
	.plus {
		z-index: 3;
	}
}
