*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
}
html, body {
	height: 100%;
	font-family: 'Roboto', sans-serif;
}

.wrapper {
	display: flex;
	flex-direction: column;
	height: 100%;
}

.content {
	flex: 1 0 auto;
}

.footer {
	flex: 0 0 auto;
}

.container {
	margin: 0 auto;
	width: 1400px;
	height: 100%;
}

.title {
	font-style: normal;
	font-weight: 500;
	font-size: 64px;
	line-height: 75px;
	color: $color-black;
	margin: 150px 0 0;
	padding: 0;
}

.subtitle {
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 28px;
	text-transform: uppercase;
	margin: 0;
	padding: 0;
}

.main-color {
	color: $color-main;
}

.par {
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 35px;
	margin: 0;
	padding: 0;
}

.input {
	border: 1px solid $color-main;
	box-sizing: border-box;
	border-radius: 3px;
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 21px;
	padding: 15px 30px 12px;
	outline: none;
	&::placeholder {
		color: $color-black;
	}
}

.textarea {
	display: block;
	overflow: hidden;
	resize: none;
}

.checkbox input {
	position: absolute;
	z-index: -1;
	opacity: 0;
	margin: 0;
}
.checkbox__text {
	position: relative;
	padding: 0 0 0 30px;
	cursor: pointer;
}
.checkbox__text:before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 13px;
	height: 13px;
	border: 1px solid $color-black;
}
.checkbox__text:after {
	display: none;
	content: url("/images/sprites/svg/check.svg");
	position: absolute;
	top: -1px;
	left: 2px;
	width: 9px;
	height: 7px;
	transition: .2s;
}

.checkbox input:checked + .checkbox__text:after {
	display: block;
}

.main-link {
	display: flex;
	align-items: center;
	text-decoration: none;
	outline: none;
	cursor: pointer;
	& > span {
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 28px;
		color: $color-main;
		margin-right: 56px;
	}
	& > svg {
		fill: $color-main;
		width: 20px;
		height: 15px;
	}

	&--min {
		& > span {
			margin-right: 23px;
		}
		& > svg {
			width: 11px;
			height: 7px;
		}
	}
}

.ui-tabs {
	padding: 0;
}

.ui-tabs .ui-tabs-panel {
	padding: 0;
}

.ui-tabs .ui-tabs-nav {
	padding: 0;
	height: 64px;
}
.ui-tabs .ui-tabs-nav li {
	margin: 0;
	.ui-tabs-anchor {
		background: transparent;
		border: 1px solid transparent;
		box-sizing: border-box;
		border-radius: 50px;
		padding: 19px 27px 15px;
		font-style: normal;
		font-weight: 500;
		font-size: 24px;
		line-height: 28px;
		outline: none;
		cursor: pointer;
		color: $color-black;
	}
}

.ui-tabs .ui-tabs-nav li.ui-tabs-active {
	margin: 0;
	padding: 0;
	.ui-tabs-anchor {
		border-color: $color-main;
	}
}

.ui-selectmenu-button.ui-button {
	position: relative;
	display: flex;
	width: 230px;
	align-items: center;
	cursor: pointer;
	outline: none;
	&::after {
		content: url("/images/sprites/svg/arrow-down.svg");
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		display: flex;
		width: 10px;
		height: 10px;
		transition: all .3s;
	}
	&.ui-selectmenu-button-closed {

	}

	&.ui-selectmenu-button-open {
		color: $color-main;
		&::after {
			transform: translateY(0%) rotate(-180deg);
		}
	}
}

.ui-selectmenu-text {
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 28px;
}

.ui-selectmenu-menu .ui-menu {
	list-style: none;
	margin: 0;
	border: 1px solid $color-black;
	border-radius: 6px;
	padding: 10px 0;
	background: $color-white;
	z-index: 5;
	li.ui-menu-item {
		width: 100%;
		& > div {
			width: 100%;
			margin: 0;
			border: none;
			background: transparent;
			display: flex;
			padding: 10px;
			font-style: normal;
			font-weight: normal;
			font-size: 20px;
			line-height: 24px;
			cursor: pointer;
			outline: none;
			&:hover {
				background: $color-white-active;
			}
			&.ui-state-active {
				background: $color-white-active;
			}
		}
	}
}
