.banner {
	position: relative;
	overflow: hidden;
	height: 804px;
	background: linear-gradient(291.23deg, #F37121 66.3%, #FDC489 99.55%);
	.container {
		height: 100%;
	}
}

.banner__ellipse {
	position: absolute;
	left: 45.25%;
	top: -150px;
	z-index: 1;
	border-radius: 50%;
	width: 2100.08px;
	height: 2100.08px;
	background: linear-gradient(311deg, #F37121 75%, #FDC489 99.55%);
	transform: translate(100%, 100%);
	transition: all 2s;
}

.banner__ellipse--active {
	transform: translate(0, 0);
}

.banner__group-of-squares {
	position: absolute;
	left: 60%;
	top: 107px;
	z-index: 2;
	width: max-content;
	transform: translateX(100%);
	transition: all 2s;
}

.banner__group-of-squares--active {
	transform: translateX(0);
}

.plus {
	position: absolute;
	fill: $color-white;
	opacity: 0;
	transition: all 2s;
}

.plus--active {
	opacity: 1;
}

.plus--1 {
	left: 10.9%;
	top: 13.3%;
	width: 22px;
	height: 22px;
}

.plus--2 {
	left: 36.25%;
	top: 86.7%;
	width: 18px;
	height: 18px;
}

.plus--3 {
	left: 54.7%;
	top: 22.1%;
	width: 11px;
	height: 11px;
}

.banner__wrap {
	height: 100%;
	outline: none;
	position: relative;
	z-index: 3;
	.slick-dots {
		position: absolute;
		bottom: 50px;
		left: 48.75%;
		display: flex;
		align-items: center;
		list-style: none;
		margin: 0;
		padding: 0;
		li {
			width: 10px;
			height: 10px;
			margin: 0 5px;
		}
		li.slick-active {
			width: 15px;
			height: 15px;
		}
		button {
			color: transparent;
			width: 100%;
			height: 100%;
			margin: 0;
			padding: 0;
			border-radius: 50%;
			background: $color-white;
			border: none;
			cursor: pointer;
			outline: none;
			transition: all .3s;
			&:hover {
				background: $color-white-active;
			}
		}

	}
}

.banner__item-wrap {
	outline: none;
}

.banner__item {
	display: flex;
	height: 100%;
	outline: none;
}

.banner__info {
	width: 700px;
	flex-shrink: 0;
}

.banner__title {
	font-style: normal;
	font-weight: 500;
	font-size: 72px;
	line-height: 84px;
	margin-top: 196px;
	margin-bottom: 31px;
	color: $color-white;
}

.banner__desc {
	font-size: 24px;
	line-height: 34px;
	color: $color-white;
	margin: 0 0 54px;
}

.banner__slide {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	justify-content: flex-end;
	margin: 117px 0 0 14px;
	flex-shrink: 0;
	width: calc(100% - 700px);
	height: 100%;
	position: relative;
	z-index: 4;
	padding-bottom: 39px;
}

.banner__img {
	width: max-content;
}

.banner__slide-desc {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
	color: $color-white;
	text-transform: uppercase;
	margin-top: 36px;
	max-width: 457px;
}

