.production {
	margin-top: 150px;
	padding-bottom: 150px;
	overflow: hidden;
}

.production__wrap {
	display: flex;
	align-items: flex-start;
}

.production__title {
	width: 50%;
	margin-top: 0;
}

.production__desc {
	width: 50%;
}

.production__par {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
	margin: 21px 0;
}

.production__slider {
	margin-top: 79px;
}

.production__slide {
	width: 731px;
	height: 440px;
	margin: 0 15px;
	outline: none;
}

.production__arrow {
	bottom: -67px;
}
.production__arrow.btn-slide--left {
	right: 18.7vw;
}
.production__arrow.btn-slide--right {
	right: 15.4vw;
}
