.article__title {
	margin-bottom: 50px;
	font-size: 48px;
	line-height: 56px;
	max-width: 1125px;
}

.article__wrap {
	display: flex;
	justify-content: space-between;
}

.sidebar {
	width: 22.2%;
	flex-shrink: 0;
	.useful-articles__item {
		margin-bottom: 30px;
	}
	.useful-articles__item-img {
		height: 195px;
	}
}

.article {
	width: 74.2%;
	flex-shrink: 0;
}

.article__img {
	border-radius: 3px;
	width: 100%;
	margin-bottom: 16px;
	& > img {
		max-width: 100%;
	}
}

.article__info {
	display: flex;
	margin-bottom: 50px;
}
.article__info-item {
	width: 29%;
}

.article__info-item-title {
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 28px;
	text-transform: uppercase;
	color: #808080;
}

.article__info-item-desc {
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 26px;
}

.article__par {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
	margin: 10px 0 30px;
}

.article__list {
	margin: 0;
	padding: 10px 0 0 66px;
	list-style: none;
	& > li {
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 26px;
		position: relative;
		margin-bottom: 18px;
		max-width: 750px;
		&::before {
			content: '';
			display: flex;
			position: absolute;
			width: 6px;
			height: 6px;
			border-radius: 50%;
			background: $color-main;
			top: 11px;
			left: -16px;
		}
	}
}

.article__subtitle {
	font-style: normal;
	font-weight: 500;
	font-size: 36px;
	line-height: 120%;
	max-width: 995px;
	margin: 80px 0 40px;
}

.article__desc {
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 26px;
	margin-top: 40px;
	margin-bottom: 30px;
}

.article__block {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
	position: relative;
	margin: 44px 0 40px;
	padding-left: 66px;
	max-width: 820px;
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 50px;
		width: 1px;
		height: 100%;
		background: $color-main;
	}
}

.article__social-wrap {
	display: flex;
	margin-top: 57px;
}
.article__social-title {
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 28px;
	text-transform: uppercase;
	margin-right: 70px;
}

.article__social {
	display: flex;
	align-items: center;
	& > a {
		margin-right: 35px;
		display: flex;
		justify-content: center;
		align-items: center;
		& > svg {
			max-width: 20px;
			max-height: 20px;
			fill: #4D4D4D;;
		}
		&:hover {
			& > svg {
				fill: $color-black;
			}
		}
	}
}

.article__title-bottom {
	font-style: normal;
	font-weight: 500;
	font-size: 36px;
	line-height: 120%;
	margin: 150px 0 40px;
}

.article__wrapper {
	padding-bottom: 100px;
}
