
.about {
	margin-top: 125px;
}

.about__wrap {
	display: flex;
}

.about__left {
	width: 50%;
	flex-shrink: 0;
}

.about__title {
	margin-top: 0;
	margin-bottom: 50px;
}

.about__par {
	margin-bottom: 34px;
	width: 82%;
}
.about__btn {
	margin-top: 37px;
}

.about__right {
	flex-shrink: 0;
	width: 50%;
	padding-left: 30px;
}

.about__list {
	list-style: none;
	margin: 30px 0 0 ;
	padding: 0;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	max-height: 700px;
	min-width: 670px;
}

.about__item {
	margin-bottom: 40px;
}

.about__item-img {
	display: flex;
	align-items: flex-end;
	& > svg {
		fill: $color-main;
	}
}

.about__plus {
	margin-right: 10px;
	width: 20px;
	height: 20px;
}
.about__svg {
	max-width: 75px;
	max-height: 65px;
}

.about__item-title {
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 28px;
	max-width: 240px;
	margin-top: 40px;
	margin-bottom: 14px;
	height: 56px;
}

.about__item-desc {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
	max-width: 295px;
}

.about__img-bottom {
	margin-top: 55px;
}
