
.partnership {
	margin-top: 251px;
}


.partnership__buttons {
	display: flex;
	width: 100%;
	justify-content: space-between;
	margin-top: 36px;
	margin-bottom: 50px;
	align-items: center;
}

.partnership__cities {
	display: flex;
	flex-wrap: wrap;
	list-style: none;
	padding: 0;
	margin: 0;
}

.partnership__item-button {
	display: flex;
	align-items: center;
	background: transparent;
	border: none;
	padding: 0;
	margin: 0;
	cursor: pointer;
	outline: none;
	& > span {
		font-style: normal;
		font-weight: 600;
		font-size: 18px;
		line-height: 22px;
		margin-right: 35px;
	}
	& > svg {
		width: 10px;
		height: 10px;
	}
	&:hover {
		& > span {
			color: $color-main;
		}
	}
}

.partnership__shops {
	display: none;
	margin: 0;
	list-style: none;
	padding: 0;
	flex-wrap: wrap;
	margin-top: 30px;
}

.partnership__store {
	width: 33%;
	margin-bottom: 40px;
}

.partnership__store-address {
	margin-bottom: 20px;
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 22px;
}

.partnership__store-phone {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 22px;
	text-decoration: none;
	color: $color-black;
}

.partnership__city {
	width: 33.3%;
	margin-bottom: 60px;
	&--active {
		width: 100%;
		.partnership__item-button {
			& > span {
				color: $color-main;
			}
			& > svg {
				display: none;
			}
		}
		.partnership__shops {
			display: flex;
		}
	}
	&--hidden {
		display: none;
	}

}

