.none-banner {
	background: $color-white;
	padding-top: 83px;
	position: relative;
	overflow: hidden;
	height: 521px;
}

.none-banner__wrap {
	position: relative;
	z-index: 3;
	display: flex;
	justify-content: space-between;
}

.none-banner__text {
	position: relative;
	z-index: 3;
	background: $color-white;
}

.none-banner__404 {
	font-style: normal;
	font-weight: normal;
	font-size: 288px;
	line-height: 337px;
	letter-spacing: 0.03em;
	color: $color-main;
}

.none-banner__title {
	color: $color-main;
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 28px;
	letter-spacing: 0.03em;
	margin: 88px 0 24px;
}

.none__par {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 120%;
	max-width: 275px;
	margin: 0 0 26px;
}


.none-banner__sguares {
	position: absolute;
	right: -56%;
	top: 0;
	.square {
		background: $color-main;
	}
}

.none-banner__sguares-reverse {
	position: absolute;
	top: 63%;
	left: -41%;
	.squares {
		flex-direction: row-reverse;
	}
	.square {
		background: $color-main;

	}
}

.none-banner__pluses {
	.plus {
		z-index: 3;
		fill: $color-main;
	}
}
