.partners {
	padding-top: 60px;
	padding-bottom: 150px;
}

.partners__title {
	margin-top: 0;
}

.partners__par {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
	max-width: 450px;
	margin-top: 21px;
	margin-bottom: 37px;
}


.partners__slide {
	width: 425px;
	height: 352px;
	border: 1px solid #F57E23;
	box-sizing: border-box;
	border-radius: 3px;
	margin-right: 15px;
	outline: none;

	& > div {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		& > img {
			max-width: 100%;
			max-height: 100%;
		}
	}
	.partners__img-hover {
		display: none;
	}

	&:hover {
		background: $color-main;
		.partners__img {
			display: none;
		}
		.partners__img-hover {
			display: flex;
		}
	}
}

.partners__arrow {
	bottom: -69px;
}
