.documents {
	padding-top: 68px;
	padding-bottom: 82px;
}

.documents__wrap {
	display: flex;
}

.documents__title {
	width: 33.5%;
	margin-top: 0;
}

.documents__documents {
	width: 66.5%;
	display: flex;
	flex-wrap: wrap;
}
.documents__document {
	width: 50%;
	min-height: 114px;
	margin-bottom: 35px;
	display: flex;
	text-decoration: none;
	& > svg {
		width: 63px;
		height: 80px;
		flex-shrink: 0;
	}
}

.documents__document-text {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
	color: $color-main;
	margin-left: 46px;
}

.documents__document-title {
	margin-bottom: 10px;
	max-width: 292px;
	display: flex;
	align-items: center;
	& > svg {
		width: 15px;
		height: 15px;
		fill: $color-main;
		margin-left: 23px;
	}
}
