.partnership-banner {
	background: $color-white;
	padding-top: 150px;
	height: 813px;
	position: relative;
	overflow: hidden;
}

.partnership-banner__wrap {
	position: relative;
	z-index: 3;
	display: flex;
	justify-content: space-between;
}

.partnership-banner__form {
	display: flex;
	flex-direction: column;
	.btn__text {
		display: flex;
		align-items: center;
	}
}

.partnership-banner__subtitle {
	font-style: normal;
	font-weight: 500;
	font-size: 36px;
	line-height: 42px;
	margin: 12px 0 22px;
	color: $color-main;
}

.partnership-banner__form-par {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
	margin: 0 0 24px;
	max-width: 336px;
}

.partnership-banner__input {
	width: 336px;
	margin-bottom: 20px;
	&--phone {
		margin-bottom: 10px;
	}
}

.partnership-banner__person {
	margin-bottom: 30px;
	.checkbox__text {
		font-size: 12px;
	}
	a {
		color: $color-black;
	}
}

.partnership-banner__title {
	font-style: normal;
	font-weight: 500;
	font-size: 72px;
	line-height: 84px;
	margin: 0 0 52px;
	color: $color-black;
	max-width: 665px;
}

.partnership__par {
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 34px;
	max-width: 520px;
}

.partnership-banner__title-two {
	font-style: normal;
	font-weight: 500;
	font-size: 64px;
	line-height: 75px;
	margin: 47px 0 50px;
	color: $color-black;
	max-width: 520px;
	position: relative;
	z-index: 4;
	background: $color-white;
}

.partnership-banner__sguares {
	position: absolute;
	right: -56%;
	top: 0;
	.square {
		background: $color-main;
	}
}

.partnership-banner__sguares-reverse {
	position: absolute;
	top: 47%;
	left: -41%;
	.squares {
		flex-direction: row-reverse;
	}
	.square {
		background: $color-main;

	}
}

.partnership-banner__pluses {
	.plus {
		z-index: 3;
		fill: $color-main;
	}
}


.partnership-map {
	margin-top: 0;
}
