.production-list {

}

.production-item {
	display: flex;
	padding-top: 320px;
}

.production-item__info {
	width: 50%;
}

.production-item__title {
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 28px;
	margin: 0 0 20px;
}

.production-item__desc {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
	max-width: 524px;
	margin: 0 0 30px;
}

.production-item__img-wrap {
	width: 50%;
}

.production-item__img {
	width: 100%;
	height: 441px;
	background: $color-main;
	position: relative;
	& > img {
		max-height: 576px;
		position: absolute;
		bottom: 71px;
		right: 75px;
	}
}

.production-item--reverse {
	flex-direction: row-reverse;
	.production-item__info {
		padding-left: 64px;
	}
	.production-item__img {
		& > img {
			right: auto;
			left: 70px;
			&.production-item__img-min {
				left: 180px;
			}
		}
	}
}
