.card {
	padding-bottom: 70px;
}

.card__wrap {
	display: flex;
}

.card__img {
	width: 50%;
	padding-top: 90px;
}

.card__info {
	width: 50%;
}

.card__title {
	margin: 3px 0 57px;
	font-size: 72px;
}

.card__subtitle {
	font-style: normal;
	font-weight: 500;
	font-size: 36px;
	line-height: 42px;
	margin: 0 0 23px;
}

.card__par {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
	max-width: 700px;
	margin: 0 0 30px;
}

.card__min-subtitle {
	font-style: normal;
	font-weight: 500;
	font-size: 16px;
	line-height: 19px;
	text-transform: uppercase;
	margin: 40px 0 32px;
}

.card__list {
	margin: 0;
	padding: 0;
	list-style: none;
	max-width: 590px;
	li {
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 24px;
		margin-bottom: 12px;
		&::before {
			content: '—';
			font-style: normal;
			font-weight: normal;
			font-size: 18px;
			line-height: 26px;
			margin-right: 5px;
		}
	}
}

.card-maps {
	.partnership {
		margin-top: 100px;
	}
	.partnership__title {
		margin-top: 100px;
	}
}
