.card-info {
	padding-bottom: 70px;
}

.ui-tabs .ui-tabs-nav.card-info__tabs {
	padding: 0;
	height: 49px;
}
.ui-tabs .ui-tabs-nav.card-info__tabs li.card-info__tab {
	margin: 0;
	.ui-tabs-anchor {
		font-style: normal;
		font-weight: 500;
		font-size: 16px;
		line-height: 19px;
		padding: 0 43px 29px;
		border: none;
		border-radius: 0;
		text-transform: uppercase;
		border-bottom: 1px solid #C4C4C4;
		color: $color-black;
	}
	&:first-child {
		.ui-tabs-anchor{
			padding-left: 0;
		}
	}
	&:last-child {
		.ui-tabs-anchor {
			padding-right: 0;
		}
	}
}

.ui-tabs .ui-tabs-nav.card-info__tabs li.card-info__tab.ui-tabs-active {
	margin: 0;
	padding: 0;
	.ui-tabs-anchor {
		border-color: $color-main;
	}
}

.card-info__specifications-list {
	list-style: none;
	margin: 0;
	padding: 30px 0 0;
}

.card-info__specifications-item {
	display: flex;
	margin-bottom: 23px;
}

.card-info__specifications-item-title {
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 28px;
	color: $color-main;
	width: 300px;
	margin-right: 50px;
}

.card-info__specifications-item-desc {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 21px;
}

.card-info__safety-precautions {
	padding-top: 50px;
}


.card-info__safety-precautions__list {
	margin: 0;
	padding: 0;
	list-style: none;
	display: flex;
	flex-direction: column;
	min-width: 769px;
}

.card-info__safety-precautions__item {
	margin-bottom: 41px;
	position: relative;
}

.card-info__safety-precautions__item-number {
	position: absolute;
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 24px;
	bottom: 70%;
}

.card-info__safety-precautions__item-text {
	display: inline-block;
	margin-left: 35px;
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
	max-width: 450px;
}

.card-info__contraindications {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
	padding-top: 52px;
}

.card-info__certificates {
	padding-top: 50px;
	.documents__document {
		margin-bottom: 50px;
	}
	.documents__document-title {
		max-width: 250px;
	}
}
