.contact-banner {
	background: $color-main;
	padding-top: 150px;
	height: 620px;
	position: relative;
	overflow: hidden;
}

.contact-banner__wrap {
	position: relative;
	z-index: 3;
}

.contact-banner__title {
	font-style: normal;
	font-weight: 500;
	font-size: 72px;
	line-height: 84px;
	margin: 0 0 52px;
	color: $color-white;
	max-width: 665px;
}


.contact-banner__sguares {
	position: absolute;
	right: -56%;
	top: 16px;
}

.contact-banner__pluses {
	.plus {
		z-index: 3;
	}
}

.contact-banner__list {
	list-style: none;
	margin: 0;
	padding: 0;
}

.contact-banner__item {
	display: flex;
	margin-bottom: 51px;
}
.contact-banner__item-title {
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 28px;
	text-transform: uppercase;
	color: $color-white;
	width: 230px;
}

.contact-banner__item-desc {
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 34px;
	color: $color-white;
	text-decoration: none;
}

.content--contact {
	#map {
		width: 100%;
		height: 600px;
	}
}
