.company-banner {
	height: 804px;
	padding-top: 190px;
	background-image: url("/images/about-company-banner.jpg");
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
}

.company-banner__title {
	margin: 0 0 30px;
	padding: 0;
	color: $color-white;
	font-size: 72px;
	line-height: 84px;
}

.company-banner__par {
	color: $color-white;
	max-width: 730px;
}

.company-banner__play {
	position: absolute;
	right: 20%;
	top: 50%;
	transform: translateY(-50%);
	cursor: pointer;
	& > svg {
		fill: $color-white;
		width: 48px;
		height: 54px;
		transition: all .3s;
	}
	& > span {
		display: block;
		margin-top: 15px;
		font-style: normal;
		font-weight: normal;
		font-size: 16px;
		line-height: 19px;
		color: $color-white;
		transition: all .3s;
	}
	&:hover {
		& > svg {
			fill: $color-white-active;
		}
		& > span {
			color: $color-white-active;
		}
	}
}
