.questions-banner {
	background: $color-main;
	padding-top: 150px;
	height: 790px;
	position: relative;
	overflow: hidden;
}

.questions-banner__wrap {
	position: relative;
	z-index: 3;
}

.questions-banner__title {
	font-style: normal;
	font-weight: 500;
	font-size: 72px;
	line-height: 84px;
	margin: 0 0 30px;
	color: $color-white;
	max-width: 665px;
}


.questions-banner__sguares {
	position: absolute;
	right: -56%;
	top: 100px;
}

.questions-banner__ellipse {
	position: absolute;
	z-index: 0;
	width: 1056px;
	height: 1056px;
	left: 55px;
	top: -267px;
	background: linear-gradient(147.31deg, #F57E23 26.7%, #FDC489 119.84%);
	border-radius: 50%;
}

.questions-banner__pluses {
	.plus {
		z-index: 3;
	}
}
