.service-banner {
	background: $color-white;
	padding-top: 150px;
	height: 663px;
	position: relative;
	overflow: hidden;
}

.service-banner__wrap {
	position: relative;
	z-index: 3;
}

.service-banner__title {
	font-style: normal;
	font-weight: 500;
	font-size: 72px;
	line-height: 84px;
	margin: 0 0 52px;
	color: $color-black;
	max-width: 665px;
}

.service-banner__title-two {
	font-style: normal;
	font-weight: 500;
	font-size: 64px;
	line-height: 75px;
	margin: 95px 0 50px;
	color: $color-black;
	max-width: 665px;
}

.service-banner__sguares {
	position: absolute;
	right: -56%;
	top: 0;
	.square {
		background: $color-main;
	}
}

.service-banner__pluses {
	.plus {
		z-index: 3;
		fill: $color-main;
	}
}

.service-banner__list {
	list-style: none;
	margin: 0;
	padding: 0;
}

.service-banner__item {
	display: flex;
	margin-bottom: 51px;
}
.service-banner__item-title {
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 28px;
	text-transform: uppercase;
	color: $color-black;
	width: 230px;
}

.service-banner__item-desc {
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 34px;
	color: $color-black;
	text-decoration: none;
}

.service-map {
	margin-top: 0;
}
