.diary {

}

.diary__wrap {
	display: flex;
	justify-content: space-between;
	margin-top: 50px;
}

.diary__img {
	position: absolute;
	right: 0;
	bottom: -100%;
	transition: all .3s;
}

.diary__item {
	width: 674px;
	background: #FFFFFF;
	border: 1px solid #F57E23;
	box-sizing: border-box;
	border-radius: 3px;
	padding: 50px 40px 46px;
	overflow: hidden;
	position: relative;
	.documents__document {
		margin-bottom: 0;
		min-height: 78px;
	}
	&:hover {
		.diary__img {
			bottom: 0;
		}
	}
}

.diary__item-title {
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 28px;
	text-transform: uppercase;
	margin: 0 0 30px;
	padding: 0;
}

.diary__item-desc {
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 34px;
	margin: 0 0 30px;
	padding: 0;
	max-width: 283px;
}
