.useful-articles {

}

.useful-articles__title {
	margin-bottom: 50px;
}

.useful-articles__wrap {
	margin-top: 50px;
}

.useful-articles__top {
	width: 100%;
	display: flex;
	justify-content: space-between;
}

.useful-articles__top-list {
	width: 48%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	flex-shrink: 0;
}

.useful-articles__bottom {
	display: flex;
	justify-content: space-between;
	margin-top: 26px;
	padding-bottom: 6px;
}

.useful-articles__item {
	width: 311px;
	margin-bottom: 0;
}

.useful-articles__item-img {
	width: 311px;
	height: 185px;
	display: flex;
	justify-content: center;
	align-items: center;
	& > img {
		max-width: 100%;
		max-height: 100%;
	}
}

.useful-articles__item-title {
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 18px;
	text-transform: uppercase;
	color: #808080;
	margin-top: 9px;
	margin-bottom: 5px;
}

.useful-articles__item-subtitle {
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 27px;
	height: 46px;
}

.useful-articles__item-desc {
	font-style: normal;
	font-weight: normal;
	font-size: 14px;
	line-height: 28px;
	color: #808080;
}

.useful-articles__big {
	width: 52%;
	flex-shrink: 0;
	.useful-articles__item {
		width: 100%;
	}
	.useful-articles__item-img {
		width: 92.5%;
		height: 495px;
	}
	.useful-articles__item-title {
		margin-bottom: 17px;
	}
	.useful-articles__item-subtitle {
		max-width: 486px;
		font-size: 20px;
		height: 55px;
	}
}
