
.advantage {
	margin-top: 155px;
}

.advantage__wrap {
	display: flex;
}

.advantage__left {
	width: 50%;
}

.advantage__title {
	margin-top: 0;
	max-width: 80%;
}

.advantage__img {
	margin-top: 111px;
	& > img {
		transform: translateX(-20px);
	}
}

.advantage__right {
	width: 50%;
}

.advantage__par {
	margin-top: 12px;
}

.advantage__subtitle {
	margin-top: 37px;
}

.advantage__list {
	list-style: none;
	margin: 35px 0 29px;
	padding: 0;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	max-height: 300px;
}

.advantage__item {
	margin-bottom: 20px;
	width: 50%;
}

.advantage__item-title {
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 28px;
	margin-bottom: 7px;
}

.advantage__item-desc {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 26px;
}
