.production-banner {
	background: $color-main;
	padding-top: 146px;
	height: 494px;
	position: relative;
	overflow: hidden;
}

.production-banner__wrap {
	position: relative;
	z-index: 3;
}

.production-banner__title {
	font-style: normal;
	font-weight: 500;
	font-size: 72px;
	line-height: 84px;
	margin: 0 0 40px;
	color: $color-white;
	max-width: 665px;
}

.production-banner__par {
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 34px;
	margin: 0;
	color: $color-white;
	max-width: 846px;
}

.production-banner__sguares {
	position: absolute;
	right: -56%;
	top: 25px;
}

.production-banner__ellipse {
	position: absolute;
	z-index: 0;
	width: 1056px;
	height: 1056px;
	left: -54px;
	top: -80px;
	background: linear-gradient(147.31deg, #F57E23 26.7%, #FDC489 119.84%);
	border-radius: 50%;
}

.production-banner__pluses {
	.plus {
		z-index: 3;
	}
}
